import debounce from 'debounce'
import { useCallback, useEffect, useRef } from 'react'

export function useDebounceCallback(cb: AnyFunction, time: number = 300) {
  const memoCb = useCallback(cb, [])
  const debounced = useRef(debounce(memoCb, time))
  useEffect(() => debounced.current.clear)
  return debounced.current
}
