import { useObservable, useObserver } from 'mobx-react-lite'
import React from 'react'

import {
  composeSelectControlStyles,
  SelectControl,
} from '../atoms/SelectControl'
import { GAddressSuggest } from '../graph/types'
import { formatAddress } from '../helpers/formats'
import { RM } from '../helpers/ramda'
import { useDebounceCallback } from '../hooks/useDebounceCallback'
import { useExecutableQuery } from '../hooks/useExecutableQuery'
import { i18n } from '../i18n'
import { AddressSuggestQuery } from './AddressSuggest.graphql'

export interface IAddressSuggestLocation
  extends Omit<GAddressSuggest.Suggest, '__typename'> {}

export interface IAddressWithGps extends IAddressSuggestLocation {
  gps: Maybe<GeoPoint>
}

interface IProps {
  onSelect(option: IAddressSuggestLocation): void
  onClear(): void
  selectedLocation?: Maybe<IAddressSuggestLocation>
  fixedLocations?: ReadonlyArray<IAddressWithGps>
  fixedLocationsLabel?: string
  minSearchLength?: number
  inputPlaceholder?: string
}

const getSuggestsWithGps = (
  suggests: ReadonlyArray<IAddressSuggestLocation>,
) => {
  return suggests.filter(RM.propSatisfies(Boolean, 'gps')) as IAddressWithGps[]
}

export const AddressSuggest: React.FC<IProps> = ({
  onSelect,
  onClear,
  fixedLocations,
  fixedLocationsLabel,
  selectedLocation,
  minSearchLength = 3,
  inputPlaceholder,
}) => {
  const state = useObservable({
    options: [] as IAddressWithGps[],
    isSearching: false,
  })

  const { execute } = useExecutableQuery<
    GAddressSuggest.Query,
    GAddressSuggest.Variables
  >(AddressSuggestQuery)

  const updateOptions = useDebounceCallback(async (input: string) => {
    state.isSearching = true
    const search = input.trim()
    if (search.length < minSearchLength) {
      state.options = []
      state.isSearching = false
      return
    }
    const result = await execute({ search })
    if (result.data) {
      state.options = getSuggestsWithGps(result.data.suggest)
    }
    state.isSearching = false
  })

  const makeOptions = () => {
    if (!fixedLocations || fixedLocations.length === 0) {
      return state.options
    }
    return [
      {
        label: fixedLocationsLabel,
        options: [...getSuggestsWithGps(fixedLocations)],
      },
      {
        label: i18n.t`Matching to input`,
        options: state.options,
      },
    ]
  }

  return useObserver(() => (
    <SelectControl<IAddressSuggestLocation>
      styles={composeSelectControlStyles({
        control: base => ({ ...base, min_width: '8rem' }),
      })}
      getOptionLabel={formatAddress}
      options={makeOptions()}
      placeholder={inputPlaceholder}
      value={selectedLocation}
      filterOption={RM.T}
      isClearable={true}
      isLoading={state.isSearching}
      onInputChange={input => {
        updateOptions(input)
      }}
      onChange={value => {
        if (value && !Array.isArray(value)) {
          onSelect(value)
        } else {
          onClear()
        }
      }}
    />
  ))
}
