import { Observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { FormInputTextBox } from '../../../atoms/FormInputTextBox'
import { FormInputTextBoxMultiline } from '../../../atoms/FormInputTextBoxMultiline'
import { HorizontalSeparator } from '../../../atoms/HorizontalSeparator'
import { IconLine } from '../../../atoms/IconLine'
import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'
import { OrderFormAutofill } from '../molecules/OrderFormAutofill'
import { OrderFormBranch } from '../molecules/OrderFormBranch'
import { OrderFormCustomerLookup } from '../molecules/OrderFormCustomerLookup'
import { OrderFormDeliveryAddress } from '../molecules/OrderFormDeliveryAddress'
import { OrderFormDeliveryType } from '../molecules/OrderFormDeliveryType'
import { OrderFormPromo } from '../molecules/OrderFormPromo'

const LookupResultIcon = styled.div`
  display: inline-block;
  vertical-align: middle;
  margin-left: 0.7rem;
`

export const OrderForm: React.FC = () => {
  const { createOrder } = useRoot()
  return (
    <>
      <IconLine icon="phone">
        <Observer>
          {() => (
            <FormInputTextBox
              field="phone"
              isRequired={createOrder.isDelivery}
              inputWidth={9}
              validatioName={i18n.t`Phone`}
              validationMessage={i18n.t`Phone number is required`}
              placeholder={i18n.t`Phone`.toLocaleLowerCase()}
            />
          )}
        </Observer>
        <LookupResultIcon>
          <OrderFormCustomerLookup />
        </LookupResultIcon>
      </IconLine>
      <Observer>
        {() => (
          <IconLine icon="streetview">
            <OrderFormDeliveryType field="deliveryType" />
            <OrderFormDeliveryAddress isVisible={createOrder.isDelivery} />
          </IconLine>
        )}
      </Observer>
      <IconLine icon="store">
        <OrderFormBranch />
      </IconLine>
      <HorizontalSeparator />
      <IconLine icon="person">
        <FormInputTextBox
          field="firstName"
          inputWidth={6}
          placeholder={i18n.t`First name`.toLocaleLowerCase()}
        />
        <FormInputTextBox
          field="lastName"
          inputWidth={12.7}
          placeholder={i18n.t`Last name`.toLocaleLowerCase()}
        />
      </IconLine>
      <IconLine icon="mail_outline">
        <FormInputTextBox
          field="email"
          inputWidth={20}
          placeholder={i18n.t`E-mail`.toLocaleLowerCase()}
        />
      </IconLine>
      <IconLine icon="note">
        <FormInputTextBoxMultiline
          field="note"
          inputWidthIsStretch={true}
          placeholder={i18n.t`Note`.toLocaleLowerCase()}
        />
      </IconLine>
      <IconLine icon="card_giftcard">
        <OrderFormPromo />
      </IconLine>
      <OrderFormAutofill />
    </>
  )
}
