import React from 'react'
import { OverlayView } from 'react-google-maps'

import { GOrderMapMarker } from '../../../graph/types'
import { gpsToLatLng } from '../../../helpers/gps'
import { DeliveryTimeBadge } from '../atoms/DeliveryTimeBadge'

interface IProps {
  order: GOrderMapMarker.Fragment
  onClick(): void
}

export const OrderMapMarker: React.SFC<IProps> = ({ order, onClick }) =>
  order.gps ? (
    <OverlayView
      key={order.id}
      position={gpsToLatLng(order.gps)}
      mapPaneName={OverlayView.OVERLAY_MOUSE_TARGET}
    >
      <DeliveryTimeBadge
        deliverAt={order.deliverAt}
        deliveredAt={null}
        onClick={onClick}
      />
    </OverlayView>
  ) : null
