import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GBranchDeliveryTime } from '../../../graph/types'

export const BranchDeliveryTimeQuery = gql`
  query GBranchDeliveryTime($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      defaultDeliveryTime
    }
  }
`

export const UpdateBranchDeliveryTimeMutation = gql`
  mutation MUpdateBranchDeliveryTime($branchId: ID!, $time: Int!) {
    updateDefaultDeliveryTime(id: $branchId, defaultDeliveryTime: $time) {
      id
      defaultDeliveryTime
    }
  }
`

export const QueryBranchDeliveryTime = buildQuery<
  GBranchDeliveryTime.Query,
  GBranchDeliveryTime.Variables
>(BranchDeliveryTimeQuery)
