import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { useEffectOnce } from '../../../hooks/useEffectOnce'
import { i18n } from '../../../i18n'
import { CompanyBranchPicker } from '../../../organisms/CompanyBranchPicker'

export const OrderFormBranch: React.FC = () => {
  const { user, createOrder } = useRoot()
  useEffectOnce(() => {
    if (!createOrder.branchId) {
      createOrder.selectBranch(user.defaultBranch.id)
    }
  })
  return useObserver(() => (
    <CompanyBranchPicker
      isClearable={false}
      selectedBranches={[createOrder.currentBranch.id]}
      onSelectionChange={([branchId]) => {
        if (
          !createOrder.orderId ||
          window.confirm(
            i18n.t`Changing branch will cancel current order and remove all items from the cart. Proceed?`,
          )
        ) {
          createOrder.selectBranch(branchId)
          createOrder.resetOrder()
        }
      }}
    />
  ))
}
