import gql from 'graphql-tag'

export const AcceptOrderMutation = gql`
  mutation MAcceptOrder(
    $orderId: ID!
    $customer: InputCustomerCreate!
    $note: String
  ) {
    finishOrder(
      id: $orderId
      order: {
        customer: $customer
        note: $note
        paymentMethod: CASH
        paymentGate: CASH
      }
    ) {
      userErrors
      order {
        id
      }
    }
  }
`

export const SetOrderDeliveryTimeMutation = gql`
  mutation MSetOrderDeliveryTime($orderId: ID!, $time: Int!) {
    orderNotifyCustomer(id: $orderId, minutes: $time) {
      id
      deliverAt
    }
  }
`
