import gql from 'graphql-tag'

export const OrderVamQuery = gql`
  query GOrderVam($branchId: ID!, $phone: String!) {
    vamCustomer: vamInformation(companyBranchId: $branchId, phone: $phone) {
      isFound
      email
      firstName
      lastName
      lastOrder {
        note
      }
      locations {
        street
        houseNumber
        city
        state
        gps {
          latitude
          longitude
        }
      }
    }
  }
`
