import { Trans } from '@lingui/react'
import { OrderCartLoader } from 'c2e-tools'
import { Observer } from 'mobx-react-lite'
import React, { useRef } from 'react'
import styled from 'styled-components'

import { Loading } from '../../../atoms/Loading'
import { useRoot } from '../../../atoms/RootProvider'
import { VerticalGap } from '../../../atoms/VerticalGap'
import { OrderCreateControl } from '../atoms/OrderCreateControl'
import { OrderCartItem } from '../molecules/OrderCartItem'
import { OrderCartTotals } from '../molecules/OrderCartTotals'

interface IOrderCartProps {}

const CartStyled = styled.div`
  margin: 0.1rem 0.1rem 0.1rem -2rem;
  position: relative;
`
const EmptyCartStyled = styled.div`
  margin: 0.5rem 0 0 0.5rem;
`

export const OrderCart: React.SFC<IOrderCartProps> = props => {
  const { createOrder } = useRoot()
  const previousCart = useRef<ReactNode | undefined>(undefined)
  return (
    <OrderCreateControl>
      {({ onItemRemove, onItemEdit }) => (
        <Observer>
          {() => (
            <OrderCartLoader
              key={createOrder.cartRefreshKey}
              orderId={createOrder.orderId}
              renderEmpty={() => (
                <EmptyCartStyled>
                  <Trans>Cart is empty</Trans>
                </EmptyCartStyled>
              )}
              renderLoading={() => previousCart.current || <Loading />}
              render={order => {
                const cart = (
                  <CartStyled>
                    <OrderCartTotals order={order} />
                    <VerticalGap />
                    {order.items.map(item => (
                      <OrderCartItem
                        key={item.id}
                        item={item}
                        onEdit={() => onItemEdit(item.id)}
                        onRemove={() => onItemRemove(item.id)}
                      />
                    ))}
                  </CartStyled>
                )
                previousCart.current = cart
                return cart
              }}
            />
          )}
        </Observer>
      )}
    </OrderCreateControl>
  )
}
