import React from 'react'
import styled from 'styled-components'

import { Price } from '../../../atoms/Price'
import { Colors } from '../../../styling'

interface IProps {
  code?: ReactNode | string
  label: ReactNode | string
  price: number
  hideCode?: boolean
}

const Container = styled.div`
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
`

const RecipeCode = styled.div`
  flex: 0 0 5rem;
  text-align: right;
`

const RecipeName = styled.div`
  flex: 1 0;
  padding-left: 1rem;
`

const PriceToRight = styled.div`
  flex: 0 0 4.5rem;
  text-align: right;
  color: ${Colors.greyMiddle};
`

export const OrderCartLine: React.SFC<IProps> = ({
  code,
  label,
  price,
  hideCode = false,
}) => (
  <Container>
    {!hideCode ? <RecipeCode>{code}</RecipeCode> : null}
    <RecipeName>{label}</RecipeName>
    <PriceToRight>
      <Price price={price} />
    </PriceToRight>
  </Container>
)
