import React from 'react'
import styled, { css } from 'styled-components'

import { Card } from '../../../atoms/Card'
import { GOrderMenuCategory } from '../../../graph/types'
import { Colors } from '../../../styling'

interface IProps {
  category: GOrderMenuCategory.Fragment
  isActive: boolean
  onClick(): void
}

const CategoryCard = styled(Card)`
  flex: 1;
  cursor: pointer;
  padding: 0.5rem;
  margin: 0 0.3rem 0.5rem 0;
  min-width: 4.5rem;
  max-width: 4.5rem;
  &:hover {
    background-color: ${Colors.yellow};
  }
  ${({ isActive }: { isActive: boolean }) =>
    isActive
      ? css`
          background-color: ${Colors.green};
        `
      : ''};
`

const CategoryPictogram = styled.span`
  span:before,
  span::after {
    margin: 0;
    padding: 0;
    font-size: 2.5rem;
  }
`

const CategoryName = styled.span`
  position: absolute;
  font-size: 0.7rem;
  text-transform: uppercase;
  margin-left: 0.3rem;
  width: 2rem;
`

export const OrderMenuCategory: React.FC<IProps> = ({
  category,
  isActive,
  onClick,
}) => {
  return (
    <CategoryCard isActive={isActive} onClick={onClick}>
      <CategoryPictogram>
        <span className={`flaticon-${category.pictogram}`} />
      </CategoryPictogram>
      <CategoryName>{getShortName(category.nameLabel)}</CategoryName>
    </CategoryCard>
  )
}

function getShortName(fullname: string) {
  return fullname
    .split(/\s+/)
    .map(word => word.substr(0, 3))
    .filter(word => word.length >= 3)
    .join(' ')
}
