import React from 'react'
import { Route, RouteComponentProps, Switch } from 'react-router'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { CreateOrderRoutes } from '../../../segments/createorder/routes/CreateOrderRoutes'
import { OrderMapPage } from '../pages/OrderMapPage'
import { OrderOverviewPage } from '../pages/OrderOverviewPage'

interface IOrderRoutesProps extends RouteComponentProps<{}> {}

export const OrderRoutes: React.SFC<IOrderRoutesProps> = ({ match }) => (
  <Switch>
    <SecureRoute exact={true} path={match.path} component={OrderOverviewPage} />
    <SecureRoute
      path={`${match.path}/create/:phone?`}
      component={CreateOrderRoutes}
    />
    <SecureRoute path={`${match.path}/map`} component={OrderMapPage} />
    <Route component={NotFoundPage} />
  </Switch>
)

export default OrderRoutes
