import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { withFormApi } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

export const OrderAcceptButton = withFormApi(({ formApi }) => {
  const { createOrder } = useRoot()
  return useObserver(() => (
    <TouchButton
      color={Colors.green}
      text={i18n.t`Accept order`}
      disabled={!createOrder.hasOrder || formApi.submitting}
      onClick={formApi.submitForm}
    />
  ))
})
