import { observer } from 'mobx-react-lite'
import React from 'react'

import orderNotificationSound from '../../../assets/orderNotification.mp3'
import { useRoot } from '../../../atoms/RootProvider'
import { SoundPlayInterval } from '../../../atoms/SoundPlayInterval'
import { OrderStateCategoryEnum } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { ILoaderProps } from '../organisms/OrderListLoader'

interface IProps extends ILoaderProps {}

export const OrderNotificationSound = observer<IProps>(
  ({ isLoading, categorizedOrders }) => {
    const { config, order: orderModel } = useRoot()
    if (isLoading || !categorizedOrders) {
      return null
    }
    // sound should be played as long as there are new orders
    // and there is at least one order with detail closed
    const shouldPlay = !RM.all(
      order => orderModel.list.isOrderDetailOpen(order.id),
      categorizedOrders[OrderStateCategoryEnum.NewOrders] || [],
    )
    return (
      <SoundPlayInterval
        shouldPlay={shouldPlay}
        intervalSec={config.orderListSoundIntervalSec}
        soundUrl={orderNotificationSound}
      />
    )
  },
)
