import { observer, useObserver } from 'mobx-react-lite'
import React from 'react'
import { useQuery } from 'react-apollo-hooks'
import styled from 'styled-components'

import { Card } from '../../../atoms/Card'
import Pacman from '../../../atoms/Pacman'
import { useRoot } from '../../../atoms/RootProvider'
import { GOrderMenu, GOrderMenuRecipe } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { OrderCreateControl } from '../atoms/OrderCreateControl'
import { useOrderMenuFilter } from '../hooks/useOrderMenuFilter'
import { OrderMenuCategories } from '../molecules/OrderMenuCategories'
import { OrderMenuFilter } from '../molecules/OrderMenuFilter'
import { OrderMenuRecipes } from '../molecules/OrderMenuRecipes'
import { OrderMenuQuery } from './OrderMenu.graphql'

interface IProps {
  orderMenu: ReadonlyArray<GOrderMenu.OrderMenu>
  onRecipeClick(recipe: GOrderMenuRecipe.Fragment): void
}

const AutoHeight = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
`

const FilterCard = styled(Card)`
  flex: 0 0 auto;
`

const CategoriesCont = styled.div`
  flex: 0 0 auto;
`

export const OrderMenuView: React.FC<IProps> = ({
  onRecipeClick,
  orderMenu,
}) => {
  const {
    observableFilter,
    computedRecipes,
    toggleCategory,
    searchByText,
  } = useOrderMenuFilter(orderMenu)

  return useObserver(() => (
    <AutoHeight>
      <FilterCard>
        <OrderMenuFilter
          recipeCount={computedRecipes.get().length}
          selectedCategory={
            orderMenu.find(RM.propEq('id', observableFilter.categoryId)) || null
          }
          onSearch={searchByText}
          onConfirm={() => {
            const recipes = computedRecipes.get()
            if (recipes.length === 1) {
              onRecipeClick(recipes[0])
              return true
            }
            return false
          }}
        />
      </FilterCard>
      <CategoriesCont>
        <OrderMenuCategories
          categories={orderMenu}
          activeCategory={observableFilter.categoryId}
          onCategoryClick={toggleCategory}
        />
      </CategoriesCont>
      <OrderMenuRecipes
        recipes={computedRecipes.get()}
        onRecipeClick={onRecipeClick}
      />
    </AutoHeight>
  ))
}

export const OrderMenu: React.FC = observer(() => {
  const { createOrder } = useRoot()

  const { data, loading } = useQuery<GOrderMenu.Query, GOrderMenu.Variables>(
    OrderMenuQuery,
    {
      suspend: false,
      variables: { branchId: createOrder.currentBranch.id },
    },
  )

  if (loading) {
    return <Pacman />
  }

  return (
    <OrderCreateControl>
      {({ onRecipeClick }) => (
        <OrderMenuView
          orderMenu={data && data.orderMenu ? data.orderMenu : []}
          onRecipeClick={onRecipeClick}
        />
      )}
    </OrderCreateControl>
  )
})
