import { observable } from 'mobx'
import { useObserver } from 'mobx-react-lite'
import React, { useEffect, useRef } from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'
import {
  DeliveryTimeDialog,
  IDeliveryTimeDialogApi,
} from '../../../molecules/DeliveryTimeDialog'
import { useAcceptOrder } from '../hooks/useAcceptOrder'
import { useUpdateOrder } from '../hooks/useUpdateOrder'

interface IProps {
  callingPhone?: string
  callingEventId: MaybeID
}

export const OrderFormSetup: React.FC<IProps> = ({
  callingPhone,
  callingEventId,
  children,
}) => {
  const { createOrder } = useRoot()

  // store calling phone number and possible eventId to model upon change
  useEffect(
    () => {
      if (callingPhone) {
        createOrder.userIsCalling(callingPhone, callingEventId)
      }
    },
    [callingPhone, callingEventId],
  )

  useUpdateOrder()

  const deliveryTimeDialogRef = useRef<IDeliveryTimeDialogApi | null>(null)
  const deliveryTime = observable.box<Maybe<number>>()

  const { onSubmit, onSubmitFailure } = useAcceptOrder(() => {
    return new Promise(resolve => {
      const dispose = deliveryTime.observe(change => {
        resolve(change.newValue)
        dispose()
      })
      if (deliveryTimeDialogRef.current) {
        deliveryTimeDialogRef.current.openDialog()
      }
    })
  })

  return useObserver(() => (
    <>
      <DeliveryTimeDialog
        title={i18n.t`Change time of delivery to customer (in minutes)`}
        cancelText={i18n.t`Use default branch delivery time`}
        onConfirm={minutes => deliveryTime.set(minutes)}
        onCancel={() => deliveryTime.set(null)}
        refApi={deliveryTimeDialogRef}
      />
      <Form
        values={createOrder.formValues}
        onSubmit={onSubmit}
        onSubmitFailure={onSubmitFailure}
        onChange={formState =>
          createOrder.updateFormValues(formState.values, formState.touched)
        }
      >
        {children}
      </Form>
    </>
  ))
}
