import gql from 'graphql-tag'

import { GeoPointFragment } from '../graph/fragments'

export const AddressSuggestQuery = gql`
  query GAddressSuggest($search: String!) {
    suggest: suggestGeolocation(address: $search) {
      state
      city
      street
      houseNumber
      gps {
        ...GGeoPoint
      }
    }
  }
  ${GeoPointFragment}
`
