import { Observer } from 'mobx-react'
import React from 'react'
import { GoogleMap, GoogleMapProps, withGoogleMap } from 'react-google-maps'
import styled from 'styled-components'

import { TMapModel } from '../models/MapModel'
import { Radiuses } from '../styling'

interface IBaseMapProps extends GoogleMapProps {
  model: TMapModel
}

const MapContainerStyled = styled.div`
  border-radius: ${Radiuses.Card};
  overflow: hidden;
  height: 100%;
`

const BaseMapContainer = withGoogleMap(({ children }) => <>{children}</>)

export const BaseMap: React.FC<IBaseMapProps> = ({ model, children }) => {
  const options: google.maps.MapOptions = {
    streetViewControl: false,
    mapTypeControl: false,
    fullscreenControl: false,
  }
  return (
    <BaseMapContainer
      containerElement={<MapContainerStyled />}
      mapElement={<div style={{ height: `100%` }} />}
    >
      <Observer>
        {() => (
          <GoogleMap
            ref={model.setMapRef}
            zoom={model.zoom}
            center={model.center}
            onCenterChanged={model.updatePosition}
            onZoomChanged={model.updateZoom}
            options={options}
          >
            {children}
          </GoogleMap>
        )}
      </Observer>
    </BaseMapContainer>
  )
}
