import React from 'react'
import { RouteComponentProps, StaticContext } from 'react-router'

import { Card } from '../../../atoms/Card'
import { IconLine } from '../../../atoms/IconLine'
import { i18n } from '../../../i18n'
import { PageLayout } from '../../../molecules/PageLayout'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { OrderCancelPrompt } from '../atoms/OrderCancelPrompt'
import { OrderFormSetup } from '../atoms/OrderFormSetup'
import { OrderAcceptButton } from '../molecules/OrderAcceptButton'
import { OrderCancelButton } from '../molecules/OrderCancelButton'
import { OrderCart } from '../organisms/OrderCart'
import { OrderForm } from '../organisms/OrderForm'
import { OrderMenu } from '../organisms/OrderMenu'

function getLayout() {
  return [
    { i: 'bcancelorder', x: 6, y: 0, w: 2, h: 1 },
    { i: 'bacceptorder', x: 10, y: 0, w: 3, h: 1 },
  ]
}

export const CreateOrderPage: React.SFC<
  RouteComponentProps<{ phone?: string }, StaticContext, { eventId: MaybeID }>
> = ({ match, location: { state } }) => (
  <OrderFormSetup
    callingPhone={match.params.phone}
    callingEventId={state ? state.eventId : null}
  >
    <OrderCancelPrompt />
    <PageLayout
      layouts={getLayout()}
      pageTitle={i18n.t`New order`}
      useBackButton={true}
    >
      <div key="bcancelorder">
        <OrderCancelButton />
      </div>

      <div key="bacceptorder">
        <OrderAcceptButton />
      </div>
    </PageLayout>

    <PageLayoutFullHeightItem widthInColumns={5} withScrollArea={true}>
      <Card>
        <IconLine icon="shopping_cart">
          <OrderCart />
        </IconLine>
      </Card>
      <Card>
        <OrderForm />
      </Card>
    </PageLayoutFullHeightItem>

    <PageLayoutFullHeightItem
      column={5}
      widthInColumns={11}
      withScrollArea={false}
    >
      <OrderMenu />
    </PageLayoutFullHeightItem>
  </OrderFormSetup>
)
