import { observable, when } from 'mobx'
import { useDisposable } from 'mobx-react-lite'
import { useCallback, useRef } from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import {
  GOrderPromo,
  MAddOrderPromo,
  MRemoveOrderPromo,
} from '../../../graph/types'
import { useGuardedMutation } from '../../../hooks/useGuardedMutation'
import {
  AddOrderPromoMutation,
  RemoveOrderPromoMutation,
} from '../molecules/OrderFormPromo.graphql'

type TCode = GOrderPromo.PromoCodes

export function usePromoCodes() {
  const { createOrder } = useRoot()
  const currentCodes = useRef(observable.map<ID, TCode>()).current

  const addCodeMutation = useGuardedMutation<
    MAddOrderPromo.Mutation,
    MAddOrderPromo.Variables
  >(AddOrderPromoMutation, {
    refetchQueries: ['GOrderCart'],
    getUserErrors: data => data.addOrderPromoCode.userErrors,
  })

  const addCode = useCallback(async (code: TCode) => {
    const result = await addCodeMutation({
      orderId: createOrder.orderId!,
      code: code.code,
    })
    if (result && result.addOrderPromoCode.userErrors.length) {
      currentCodes.delete(code.id)
    }
  }, [])

  // @ts-ignore
  const removeCode = useGuardedMutation<
    MRemoveOrderPromo.Mutation,
    MRemoveOrderPromo.Variables
  >(RemoveOrderPromoMutation, { refetchQueries: ['GOrderCart'] })

  useDisposable(() =>
    currentCodes.observe(change => {
      if (createOrder.orderId === null) {
        // no order yet, don't care about this change
        return
      }
      if (change.type === 'add') {
        addCode(change.newValue)
      }
      if (change.type === 'delete') {
        removeCode({ orderId: createOrder.orderId, codeId: change.oldValue.id })
      }
    }),
  )

  useDisposable(() =>
    when(
      () => createOrder.orderId !== null,
      () => {
        currentCodes.forEach(addCode)
      },
    ),
  )

  return currentCodes
}
