import { Trans } from '@lingui/react'
import Color from 'color'
import React from 'react'
import styled from 'styled-components'

import { Colors } from '../../../styling'

interface IProps {
  count: number
  isCountBold?: boolean
  isCollapsed?: boolean
  onClick(): void
}

const Container = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-end;
  cursor: pointer;
  &:hover {
    background-color: ${Color(Colors.turquoise)
      .alpha(0.05)
      .string()};
  }
  &:active {
    background-color: ${Color(Colors.turquoise)
      .alpha(0.2)
      .string()};
  }
`

const ListTitle = styled.h2`
  color: ${Colors.greyLight};
  font-weight: lighter;
  font-size: 1.6rem;
  margin: 1.2rem 0 0.8rem 0.4rem;
`

const Badge = styled.div`
  margin: 1rem 0 1rem 1rem;
  min-height: 1.8rem;
  line-height: 1.8rem;
  min-width: 1.8rem;
  border-radius: 1.8rem;
  font-size: 1.2rem;
  text-align: center;
  vertical-align: middle;
  padding: 0;
  display: inline-block;
  background-color: ${Colors.black};
  color: white;
`

const BadgeBold = styled(Badge)`
  background-color: ${Colors.red};
  color: white;
`

const ArrowUp = styled.div`
  width: 0;
  height: 0;
  border-left: 0.5rem solid transparent;
  border-right: 0.5rem solid transparent;
  border-bottom: 0.5rem solid white;
  margin: 0 0.65rem 1.3rem 1rem;
`

const ArrowDown = styled.div`
  width: 0;
  height: 0;
  border-left: 0.5rem solid transparent;
  border-right: 0.5rem solid transparent;
  border-top: 0.5rem solid white;
  margin: 0 0.65rem 1.3rem 1rem;
`

const CollapsedWarning = styled.div`
  margin: 0 0 1rem 1rem;
`

export const OrderListTitle: React.SFC<IProps> = ({
  count = 0,
  isCollapsed = false,
  isCountBold = false,
  onClick,
  children,
}) => (
  <Container onClick={onClick}>
    {isCollapsed ? <ArrowDown /> : <ArrowUp />}
    <ListTitle>{children}</ListTitle>
    {isCountBold ? <BadgeBold>{count}</BadgeBold> : <Badge>{count}</Badge>}
    {isCollapsed ? (
      <CollapsedWarning>
        <Trans>List is collapsed</Trans>
      </CollapsedWarning>
    ) : null}
  </Container>
)
