import { i18nMark } from '@lingui/react'
import React from 'react'
import { FieldType } from 'react-form'

import { FormInputFlatSelect } from '../../../atoms/FormInputFlatSelect'
import { DeliveryTypeEnum } from '../../../graph/types'
import { i18n } from '../../../i18n'

interface IProps {
  field: FieldType
}

export const types = [
  {
    type: DeliveryTypeEnum.Messenger,
    label: i18nMark('Deliver'),
  },
  {
    type: DeliveryTypeEnum.Pickup,
    label: i18nMark('Personal pickup'),
  },
]

export function OrderFormDeliveryType({ field }: IProps) {
  return (
    <FormInputFlatSelect
      field={field}
      items={types}
      formatItem={item => i18n._(item.label)}
      getItemId={item => item.type}
    />
  )
}
