import React from 'react'
import styled from 'styled-components'

import { GOrderMenuCategory } from '../../../graph/types'
import { OrderMenuCategory } from '../atoms/OrderMenuCategory'

interface IProps {
  categories: ReadonlyArray<GOrderMenuCategory.Fragment>
  activeCategory: MaybeID
  onCategoryClick(categoryId: ID): void
}

const FlexFlow = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`

export const OrderMenuCategories: React.FC<IProps> = ({
  categories,
  activeCategory,
  onCategoryClick,
}) => {
  return (
    <FlexFlow>
      {categories.map(category => (
        <OrderMenuCategory
          key={category.id}
          category={category}
          isActive={category.id === activeCategory}
          onClick={() => onCategoryClick(category.id)}
        />
      ))}
    </FlexFlow>
  )
}
