import React from 'react'
import styled from 'styled-components'

import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

interface IProps {
  label: string
  isSelected: boolean
  onClick(): void
}

const SidedishButtonContainer = styled.div`
  margin: 0 1rem 1rem 0;
  width: 8rem;
  display: inline-block;
`

export const OrderMenuSidedishButton: React.SFC<IProps> = ({
  label,
  isSelected,
  onClick,
}) => (
  <SidedishButtonContainer>
    <TouchButton
      text={label}
      onClick={onClick}
      color={isSelected ? Colors.orange : Colors.greyDark}
    />
  </SidedishButtonContainer>
)
