import { ICartOrder } from 'c2e-tools'
import React from 'react'
import styled from 'styled-components'

import { i18n } from '../../../i18n'
import { OrderCartLine } from '../atoms/OrderCartLine'

interface IProps {
  order: ICartOrder
}

const OrderCartTotalsContainer = styled.div`
  margin: 0 0 0 auto;
  text-align: right;
`

export const OrderCartTotals: React.SFC<IProps> = ({ order }) => (
  <OrderCartTotalsContainer>
    <b>
      <OrderCartLine
        label={i18n.t`Total price`}
        price={order.totalSum.value}
        hideCode={true}
      />
    </b>
    <OrderCartLine
      label={i18n.t`Transport fee`}
      price={order.transportFee.value}
      hideCode={true}
    />
    <OrderCartLine
      label={i18n.t`Covers price`}
      price={order.coverPrice.value}
      hideCode={true}
    />
    <OrderCartLine
      label={i18n.t`Discount total`}
      price={order.totalDiscount.value * -1}
      hideCode={true}
    />
  </OrderCartTotalsContainer>
)
