import { Tooltip } from '@material-ui/core'
import { Alarm } from '@material-ui/icons'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { ETimeBadgeVariant, TimeBadge } from '../../../atoms/TimeBadge'
import { MUpdateBranchDeliveryTime } from '../../../graph/types'
import { useGuardedMutation } from '../../../hooks/useGuardedMutation'
import { i18n } from '../../../i18n'
import { DeliveryTimeDialog } from '../../../molecules/DeliveryTimeDialog'
import {
  QueryBranchDeliveryTime,
  UpdateBranchDeliveryTimeMutation,
} from './BranchDeliveryTime.graphql'

interface IProps {}

export const BranchDeliveryTime = observer<IProps>(props => {
  const { order } = useRoot()

  const updateDeliveryTime = useGuardedMutation<
    MUpdateBranchDeliveryTime.Mutation,
    MUpdateBranchDeliveryTime.Variables
  >(UpdateBranchDeliveryTimeMutation)

  const branchId = order.list.firstBranchId

  if (!branchId) {
    return (
      <Tooltip title={i18n.t`Select a single branch to set delivery time`}>
        <TimeBadge variant={ETimeBadgeVariant.UNKNOWN}>
          <Alarm />
        </TimeBadge>
      </Tooltip>
    )
  }

  return (
    <DeliveryTimeDialog
      title={i18n.t`Change default time of delivery to customer (in minutes)`}
      onConfirm={minutes => {
        updateDeliveryTime({
          branchId,
          time: minutes,
        })
      }}
    >
      {({ openDialog }) => (
        <QueryBranchDeliveryTime
          variables={{ branchId }}
          render={({ companyBranch }) => (
            <TimeBadge
              variant={ETimeBadgeVariant.NORMAL}
              minutes={companyBranch.defaultDeliveryTime}
              onClick={openDialog}
            />
          )}
        />
      )}
    </DeliveryTimeDialog>
  )
})
