import { css } from 'emotion'
import React from 'react'
import { AutoSizer, Grid } from 'react-virtualized'

import { GOrderMenuRecipe } from '../../../graph/types'
import { OrderMenuRecipeCard } from '../atoms/OrderMenuRecipe'

interface IProps {
  recipes: ReadonlyArray<GOrderMenuRecipe.Fragment>
  onRecipeClick(recipe: GOrderMenuRecipe.Fragment): void
  columnCount?: number
}

const wrap = css`
  flex: 1 1 0;
`

export const OrderMenuRecipes: React.FC<IProps> = ({
  recipes,
  onRecipeClick,
  columnCount = 5,
}) => {
  const recipesCount = recipes.length
  const rowCount = Math.ceil(recipesCount / columnCount)

  return (
    <div className={wrap}>
      <AutoSizer>
        {({ width, height }) => (
          <Grid
            rowCount={rowCount}
            columnCount={columnCount}
            columnWidth={width / columnCount - 4}
            width={width}
            height={height}
            rowHeight={100}
            cellRenderer={({ columnIndex, rowIndex, key, style }) => {
              const recipeIndex = rowIndex * columnCount + columnIndex
              if (recipeIndex >= recipesCount) {
                return null
              }
              const recipe = recipes[recipeIndex]
              return (
                <div
                  style={{ ...style }}
                  key={key}
                  onClick={() => onRecipeClick(recipe)}
                >
                  <OrderMenuRecipeCard recipe={recipe} />
                </div>
              )
            }}
          />
        )}
      </AutoSizer>
    </div>
  )
}
