import { i18n } from '@lingui/core'
import { Plural } from '@lingui/react'
import React, { useCallback, useRef } from 'react'
import styled from 'styled-components'

import { FormInputStyled } from '../../../atoms/FormInputTextBox'
import { GOrderMenuCategory } from '../../../graph/types'

interface IProps {
  recipeCount: number
  selectedCategory: Maybe<GOrderMenuCategory.Fragment>
  onSearch(input: string): void
  onConfirm(): boolean
}

const FilterResults = styled.span`
  padding-left: 1rem;
`

const SelectedCategory = styled.div`
  float: right;
  margin: 0.6rem 2rem;
  font-weight: bold;
`

export const OrderMenuFilter: React.FC<IProps> = ({
  recipeCount,
  selectedCategory,
  onSearch,
  onConfirm,
}) => {
  const inputRef = useRef<HTMLInputElement>(null)
  const onChange = useCallback((ev: React.ChangeEvent<HTMLInputElement>) => {
    onSearch(ev.target.value)
  }, [])

  const onKeyDown = useCallback((ev: React.KeyboardEvent<HTMLInputElement>) => {
    if (ev.key === 'Enter') {
      if (onConfirm() === true && inputRef.current) {
        inputRef.current.value = ''
        onSearch('')
      }
    }
  }, [])

  return (
    <>
      <FormInputStyled
        onChange={onChange}
        onKeyDown={onKeyDown}
        placeholder={`${i18n.t`Code`.toLocaleLowerCase()} / ${i18n.t`Title`.toLocaleLowerCase()}`}
        autoComplete="off"
        // @ts-ignore Wrong typing for styled refs
        ref={inputRef}
      />
      <FilterResults>
        <Plural
          value={recipeCount}
          zero="No recipes found"
          one="One recipe found, Enter to confirm"
          few="Found # recipes"
          other="Found # recipes"
        />
      </FilterResults>
      {selectedCategory ? (
        <SelectedCategory>{selectedCategory.nameLabel}</SelectedCategory>
      ) : null}
    </>
  )
}
