import { autorun } from 'mobx'
import { useObservableEffect } from 'mobx-react-lite'

import { useRoot } from '../../../atoms/RootProvider'
import { MUpdateOrder } from '../../../graph/types'
import { useGuardedMutation } from '../../../hooks/useGuardedMutation'
import { UpdateOrderMutation } from './useUpdateOrder.graphql'

export function useUpdateOrder() {
  const { createOrder } = useRoot()
  const updateOrder = useGuardedMutation<{}, MUpdateOrder.Variables>(
    UpdateOrderMutation,
  )

  // update order whenever these would change
  useObservableEffect(() =>
    autorun(() => {
      if (createOrder.orderId) {
        updateOrder({
          orderId: createOrder.orderId,
          deliveryType: createOrder.deliveryType,
          gps: createOrder.gps,
        })
      }
    }),
  )
}
