import React from 'react'

import { GOrderList, OrderStateCategoryEnum } from '../../../graph/types'
import { OrderBox } from '../atoms/OrderBox'
import { OrderListTitle } from '../atoms/OrderListTitle'

interface IProps {
  category: GOrderList.OrderStateCategories
  orders: GOrderList.Orders[]
  showBranches: boolean
  isCategoryCollapsed?: boolean
  toggleCategoryCollapse(category: OrderStateCategoryEnum): void
  toggleOrderDetail(order: GOrderList.Orders): void
  isOrderDetailOpen(orderId: string): boolean
}

export const OrderListCategory: React.SFC<IProps> = ({
  orders,
  category,
  showBranches,
  isCategoryCollapsed = false,
  toggleCategoryCollapse,
  toggleOrderDetail,
  isOrderDetailOpen,
}) => (
  <>
    <OrderListTitle
      count={orders.length}
      isCountBold={category.enum === OrderStateCategoryEnum.NewOrders}
      isCollapsed={isCategoryCollapsed}
      onClick={() => toggleCategoryCollapse(category.enum)}
    >
      {category.code}
    </OrderListTitle>
    {orders.length > 0 &&
      !isCategoryCollapsed &&
      orders.map(order => (
        <OrderBox
          key={order.id}
          order={order}
          toggleOrderDetail={toggleOrderDetail}
          isOrderDetailOpen={isOrderDetailOpen}
          showBranch={showBranches}
        />
      ))}
  </>
)
