import React, { useEffect } from 'react'
import { Prompt } from 'react-router'

import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'

export const OrderCancelPrompt = () => {
  const root = useRoot()

  useEffect(() => {
    return () => root.createOrder.cancelOrder()
  }, [])

  return (
    <Prompt
      message={i18n.t`Do you really want to cancel order?`}
      when={root.createOrder.hasOrder}
    />
  )
}
