import { ICartWareItem } from 'c2e-tools'
import React from 'react'
import styled from 'styled-components'

import { TouchButton } from '../../../molecules/TouchButton'
import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'
import { Colors, Spacing } from '../../../styling'
import { OrderCartLine } from '../atoms/OrderCartLine'

interface IProps {
  item: ICartWareItem
  onRemove(): void
  onEdit(): void
}

const OrderCartItemStyled = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  border-top: 1px solid ${Colors.greyMiddle};
  padding: 0.1rem 0;
`

const TouchButtonColumn = styled.div`
  flex: 0 0 auto;
  padding-right: 0.5rem;
`
const LineColumn = styled.div`
  flex: 1 1 0;
`

export const OrderCartItem: React.SFC<IProps> = ({
  item,
  onRemove,
  onEdit,
}) => (
  <OrderCartItemStyled>
    <TouchButtonColumn>
      <TouchButtonDelete onDelete={onRemove} />
    </TouchButtonColumn>
    <TouchButtonColumn>
      <TouchButton
        onClick={onEdit}
        iconName="mode_edit"
        width={Spacing.ButtonHeight}
        height={Spacing.ButtonHeight}
      />
    </TouchButtonColumn>

    <LineColumn>
      <OrderCartLine
        key={item.id}
        code={item.recipe.code}
        label={item.recipe.nameLabel}
        price={item.price.value}
      />
      {item.sidedishes.map(sidedish => (
        <OrderCartLine
          key={sidedish.id}
          code={`+${sidedish.recipe.code}`}
          label={sidedish.recipe.nameLabel}
          price={sidedish.price.value}
        />
      ))}
    </LineColumn>
  </OrderCartItemStyled>
)
