import { Trans } from '@lingui/react'
import { Check, Warning } from '@material-ui/icons'
import { useComputed, useObserver } from 'mobx-react-lite'
import React, { useCallback } from 'react'
import styled from 'styled-components'

import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'
import {
  AddressSuggest,
  IAddressWithGps,
} from '../../../molecules/AddressSuggest'
import { Colors } from '../../../styling'

interface IProps {
  isVisible: boolean
}

const InputLine = styled(FlexRowSingle)`
  justify-content: flex-start;
`

const ZoneWarning = styled.div`
  display: inline-flex;
  align-items: center;
  color: ${Colors.violet};
`

const AddressValid = styled(Check)`
  font-size: 1rem;
  color: ${Colors.green};
`

const AddressContainer = styled.div`
  flex: 1 1 0;
`

export function OrderFormDeliveryAddress(props: IProps) {
  const { createOrder } = useRoot()

  const previousLocations = useComputed(() => {
    if (!createOrder.vamCustomer) {
      if (createOrder.fakeAddress) {
        return [createOrder.fakeAddress]
      }
      return []
    }
    const { locations } = createOrder.vamCustomer
    return locations
  })

  const onSuggestSelect = useCallback((selected: IAddressWithGps) => {
    createOrder.useLocation(selected)
  }, [])

  const status = useComputed(() => {
    if (createOrder.notDeliveryLocation) {
      return (
        <ZoneWarning>
          <Warning />
          <Trans>Address is not within any delivery zone</Trans>
        </ZoneWarning>
      )
    }
    if (createOrder.hasAddress) {
      return <AddressValid />
    }
    return null
  })

  return useObserver(() => (
    <div hidden={!props.isVisible}>
      <InputLine>
        <AddressContainer>
          <AddressSuggest
            fixedLocations={previousLocations}
            fixedLocationsLabel={i18n.t`Previous addresses`}
            selectedLocation={
              createOrder.deliveryAddress || createOrder.fakeAddress || null
            }
            onSelect={onSuggestSelect}
            onClear={createOrder.clearLocation}
            inputPlaceholder={i18n.t`Deliver to address`.toLocaleLowerCase()}
          />
          {status}
        </AddressContainer>
      </InputLine>
    </div>
  ))
}
