import React from 'react'

import { WithRoot } from '../../../atoms/WithRoot'
import { TouchButton } from '../../../molecules/TouchButton'

interface IProps {}

export const OrderFormAutofill: React.SFC<IProps> = props =>
  process.env.REACT_APP_STAGE !== 'prod' ? (
    <WithRoot
      render={({ createOrder }) => (
        <TouchButton
          text="Autofill [DEV]"
          onClick={createOrder.updateFormWithFakeValues}
        />
      )}
    />
  ) : null
