import React from 'react'
import styled from 'styled-components'

import { CanMutate } from '../../../atoms/CanMutate'
import { i18n } from '../../../i18n'
import { PageLayout } from '../../../molecules/PageLayout'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { PageLayoutItem } from '../../../molecules/PageLayoutItem'
import { CreateOrderButton } from '../../main/molecules/CreateOrderButton'
import { OrderNotificationSound } from '../atoms/OrderNotificationSound'
import { BranchDeliveryTime } from '../organisms/BranchDeliveryTime'
import { MapFullscreenButton } from '../organisms/MapFullscreenButton'
import { OrderList } from '../organisms/OrderList'
import { OrderListBranchFilter } from '../organisms/OrderListBranchFilter'
import { OrderListLoader } from '../organisms/OrderListLoader'
import { OrderMap } from '../organisms/OrderMap'

interface IOrderOverviewPageProps {}

function getLayout() {
  return [
    { i: 'filter', x: 0, y: 1, w: 6, h: 1 },
    { i: 'neworder', x: 6, y: 1, w: 2, h: 1 },
    { i: 'deliverytime', x: 8, y: 0, w: 1, h: 1 },
  ]
}

const FilterContainerStyled = styled('div')`
  margin: -0.05rem 0rem -0.05rem -0.18rem;
`

export const OrderOverviewPage: React.SFC<IOrderOverviewPageProps> = props => (
  <OrderListLoader>
    {loaderProps => (
      <>
        <OrderNotificationSound {...loaderProps} />
        <PageLayoutFullHeightItem
          withScrollArea={true}
          row={2}
          widthInColumns={8}
          scrollAreaId="order-list"
        >
          <OrderList {...loaderProps} />
        </PageLayoutFullHeightItem>

        <PageLayoutFullHeightItem
          widthInColumns={8}
          column={8}
          withScrollArea={false}
        >
          <OrderMap {...loaderProps} />
          <MapFullscreenButton />
        </PageLayoutFullHeightItem>

        <PageLayout
          layouts={getLayout()}
          pageTitle={i18n.t`Orders`}
          showSoundControl={true}
          scrollPageTitle={false}
        >
          <PageLayoutItem
            key="filter"
            component={() => (
              <FilterContainerStyled>
                <OrderListBranchFilter />
              </FilterContainerStyled>
            )}
          />
          <PageLayoutItem
            key="neworder"
            component={() => (
              <CanMutate mutationName="createOrder">
                <CreateOrderButton showOnlyText={true} />
              </CanMutate>
            )}
          />
          <PageLayoutItem
            key="deliverytime"
            component={() => <BranchDeliveryTime />}
          />
        </PageLayout>
      </>
    )}
  </OrderListLoader>
)
